import React, { memo } from 'react';
import { keyframes, createGlobalStyle } from 'styled-components';
import { Icon } from './Icon';

const SPIN_KEYFRAMES = keyframes`
100% {
  transform: rotate(360deg);
}
`;
const SpinGlobalStyle = createGlobalStyle`
._wdn-icon-spin{
  animation: ${SPIN_KEYFRAMES} 1s infinite linear;
}
`;
export const IconSpin = memo(
  (props: React.ComponentProps<typeof Icon>) => {

    return <>
      <SpinGlobalStyle />
      <Icon {...props} className='_wdn-icon-spin' />
    </>;
  }
);
IconSpin.displayName = 'IconSpin';